<template>
  <tas-base-crud :key="mainKey" :config="config" hide-create hide-update hide-detail>
    <template v-slot:list-action>
      <a v-if="$_sys.isAllowed(config.permission.create)" v-b-modal.modal-create-simpan-pinjam class="btn btn-primary btn-icon btn-circle ml-3">
        <i class="ri-add-line p-0"></i>
      </a>
      <b-modal id="modal-create-simpan-pinjam" hide-footer centered size="md" :title="'Buat '+config.title">
        <div class="">
          <div class="form-group mb-2">
            <span>Periode <span class="text-danger">*</span></span>
            <s-input @input="checkThisMonth()" v-model="firstForm.periode_month" :item="{ type:'month', validation:[], placeholder: 'Pilih Periode '+config.title, api:'', formatter:momentFormat, ic:'w-100 mt-0' }" />
          </div>
          <div class="form-group mb-2">
            <span>Deskripsi</span>
            <s-input v-model="firstForm.description" :item="{ type:'textarea', rows:3, validation:[], placeholder: 'Tuliskan keterangan / deskripsi '+config.title, api:'', formatter:dateFormat, ic:'w-100 mt-0' }" />
          </div>
          <p class="mb-1"> Sebelum membuat <b>{{ config.title }}</b>, pastikan konfigurasi modul di bawah ini sudah sesuai dengan periode yang dipilih :</p>
          <ul class="pl-0 mt-0 mb-7 list-unstyled">
            <li class="d-flex align-items-center pointer arrow-info-inside" @click="redirect('periode-penggajian')">
              <i v-if="firstFormCheck.period.available" class="ri-checkbox-line text-success mr-2"></i>
              <i v-else class="ri-checkbox-blank-line text-danger mr-2"></i> Periode Penggajian <b-spinner v-if="firstFormCheck.period.loading" small class="ml-2"></b-spinner>
              <i v-else class="ri-arrow-right-line icon-arrow"></i>
            </li>
            <li v-if="(firstForm.periode_month && !firstFormCheck.period.loading && !firstFormCheck.period.available)" class="font-size-sm text-danger">Periode Penggajian {{ firstForm.periode_month | parse('period') }} belum dibuat !</li>
            <li class="d-flex align-items-center pointer arrow-info-inside" @click="redirect('periode-penggajian')">
              <i v-if="firstFormCheck.presensi.available" class="ri-checkbox-line text-success mr-2"></i>
              <i v-else class="ri-checkbox-blank-line text-danger mr-2"></i>Rekap Presensi <b-spinner v-if="firstFormCheck.presensi.loading" small class="ml-2"></b-spinner>
              <i v-else class="ri-arrow-right-line icon-arrow"></i>
            </li>
            <li v-if="(firstForm.periode_month && !firstFormCheck.presensi.loading && !firstFormCheck.presensi.available)" class="font-size-sm text-danger">Rekap Presensi {{ firstForm.periode_month | parse('period') }} belum dibuat !</li>
          </ul>
        </div>
        <div class="text-right">
          <button :disabled="!canCreate" @click="generateMonth()" class="btn btn-primary"> Buat {{ config.title }} </button>
        </div>
      </b-modal>
    </template>
    <template v-slot:list-table-action="data">
      <a @click="$children[0].getEvent('detail', data.rowData)" class="btn btn-info btn-sm"> Lihat Detail Tunjangan </a>
    </template>
    <template v-slot:header-table>
      <div class="card-body py-0">
        <div v-if="isAlertShow" class="alert alert-custom alert-light-danger p-2 mb-0" role="alert">
          <div class="alert-icon">
            <span class="svg-icon svg-icon-danger svg-icon-xl">
              <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                  <rect x="0" y="0" width="24" height="24" />
                  <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3" />
                  <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1" />
                  <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1" />
                </g>
              </svg>
            </span>
          </div>
          <div class="alert-text">Data {{ config.title }} pada bulan ini belum dibuat!</div>
        </div>
      </div>
    </template>
  </tas-base-crud>
</template>
<style scoped>
  .arrow-info-inside:hover:not(i) {
    font-weight: bold;
    color: var(--primary);
  }
  .arrow-info-inside .icon-arrow {
    transition: all 0.5s ease;
    padding-left: 0.25rem;
  }
  .arrow-info-inside:hover .icon-arrow {
    padding-left: 0.8rem;
    color: var(--primary);
  }
</style>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'crud-tunjangan-kehadiran',
  data () {
    return {
      mainKey: 1,
      isAlertShow: false,
      firstFormCheck: {
        period: { api: 'payroll-periodes', loading: false, available: false },
        presensi: { api: 'presence-on-periodes', loading: false, available: false }
      },
      firstForm: {
        periode_month: null,
        start_date: null,
        end_date: null,
        description: null
      },
      dateFormat: {
        stringify: (date) => {
          return date ? moment(date).format('DD MMMM YYYY') : null
        },
        parse: (value) => {
          return value ? moment(value, 'DD MMMM YYYY').toDate() : null
        }
      },
      momentFormat: {
        stringify: (date) => {
          return date ? moment(date).format('MMMM YYYY') : ''
        },
        parse: (value) => {
          return value ? moment(value, 'MMMM YYYY').toDate() : null
        }
      },
      config: {
        title: 'Tunjangan Kehadiran',
        model_api: 'tunjangan-kehadiran',
        getter: 'tunjangan-kehadiran',
        setter: 'tunjangan-kehadiran',
        pk_field: null,
        permission: {
          create: 'create-tunjangan-kehadiran',
          read: 'view-tunjangan-kehadiran',
          update: 'update-tunjangan-kehadiran',
          delete: 'delete-tunjangan-kehadiran'
        },
        custom_api: {
          create: 'custom/tunjangan-kehadiran'
        },
        slave: [
          {
            name: 'Rincian Presensi',
            permission: 'view-tunjangan-kehadiran-details',
            module: 'payroll/tunjangan-kehadiran-details',
            as_param: 'tunjangan_kehadiran_id',
            key_field: 'id'
          }
        ],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'periode_month', label: 'Periode', methods: { create: false, update: false, list: { transform: 'period' }, detail: { transform: 'period' } } },
          { id: 'total_penerimaan_value', label: 'Total Penerimaan', methods: { create: false, update: false, list: { transform: 'money' }, detail: { transform: 'money' } } }
        ]
      }
    }
  },
  computed: {
    canCreate () {
      // var firstForm = Object.values(this.firstForm).filter(x => !x).length
      var firstForm = Boolean(this.firstForm.periode_month)
      var check = Object.values(this.firstFormCheck).filter(x => x.available).length
      return !!(firstForm && check)
    }
  },
  mounted () {
    this.getThisMonth()
  },
  methods: {
    redirect (e) {
      this.$router.push({ name: e, query: { view: 'list' } }).catch((err) => { return err })
    },
    checkThisMonth () {
      if (this.firstForm.periode_month) {
        Object.keys(this.firstFormCheck).forEach(e => {
          this.getCheckData(e)
        })
      }
    },
    getThisMonth () {
      const formData = {
        periode_month: moment(new Date()).set('date', 1).format('YYYY-MM-DD')
      }
      this.$_api.list(this.config.getter, formData).then(res => {
        this.isAlertShow = !res.data.length
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    getCheckData (e) {
      this.$set(this.firstFormCheck[e], 'loading', true)
      const formData = {
        periode_month: moment(moment(this.firstForm.periode_month, 'MMMM YYYY').toDate()).format('YYYY-MM-DD')
      }
      this.$_api.list(this.firstFormCheck[e].api, formData).then(res => {
        this.$set(this.firstFormCheck, e, { api: this.firstFormCheck[e].api, loading: false, available: !!res.data.length })
      }).catch((err) => {
        this.$set(this.firstFormCheck[e], 'loading', false)
        this.$_alert.error(err)
      })
    },
    generateMonth () {
      const formData = {
        periode_month: moment(moment(this.firstForm.periode_month, 'MMMM YYYY').toDate()).format('YYYY-MM-DD'),
        description: this.firstForm.description
      }
      this.$_api.create(this.config.custom_api.create, formData).then(res => {
        this.$children[0].getEvent('detail', res.data)
        this.isAlertShow = false
        this.$set(this.firstForm, 'periode_month', null)
        this.$_alert.success(null, this.config.title + ' berhasil dibuat')
      }).catch((err) => {
        this.$_alert.error(err)
      })
    }
  }
}
</script>



